import React from "react";
import BannerImage from "../../assets/pizzaLeft.jpg";
import "../../styles/Contact.css";

function Contact() {
  return (
    <div className="contact">
      <div
        className="leftSide"
        style={{ backgroundImage: `url(${BannerImage})` }}
      ></div>
      <div className="rightSide">
        <h1> Contact us</h1>
        <form id="contact-form" method="POST">
          <label htmlFor="name">Full Name</label>
          <input id="name" placeholder="Enter full name..." type="text"/>
          <label htmlFor="email">Email</label>
          <input id="email" placeholder="Enter email..." type="email"/>3
          <label htmlFor="message">Message</label>
          <textarea rows="6" placeholder="Enter Message" required></textarea>
          <button type="submit">Send Message</button>
        </form>
      </div>
    </div>
  );
}

export default Contact;
